<template>
    <div class="container">
        <div
            id="toDo"
            :class="{ disabledClass: disabled }"
            @click="disabled = !disabled"
        >{{ todo.name }}</div>

        <button v-show="disabled" @click="removeToDo(todo.id)">X</button>
    </div>
</template>

<script>
export default {
    name: "ToDoElement",
    props: {
        todo: Object
    },
    methods: {
        removeToDo(id) {
            this.$store.dispatch("removeToDo", id);
        }
    },
    data() {
        return {
            disabled: false
        };
    }
};
</script>

<style scoped>
* {
    box-sizing: border-box;
}

.container {
    display: grid;
    grid-template-columns: 5fr 1fr;
    grid-gap: 10px;
}

#toDo {
    margin-bottom: 5px;
    font-size: 18px;
    text-align: left;
    background-color: #4caf50;
    padding: 12px;
    border: 2px solid #1f4e20;
    border-radius: 10px;
    cursor: pointer;
    flex: 6;
    min-width: 510px;

}

button {
    margin-bottom: 3px;
    justify-self: end;
    text-align: right;
    background-color: #222;
    border: 2px solid rgb(184, 11, 11);
    color: rgb(184, 11, 11);
    padding: 15px 23px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    flex: 1;
    
    
}

button:hover {
    background-color: #3f3d3d;
    color: rgb(226, 63, 63);
    border: 2px solid rgb(226, 63, 63);
}

button:focus {
    outline: 0;
}

.disabledClass {
    opacity: 0.4;
}
</style>