<template>
    <div id="main">
        <ToDoElement v-for="todo in getToDos" :todo="todo" :key ="todo.id" />
    </div>
</template>

<script>
import ToDoElement from "./ToDoElement.vue";
export default {
    name: "ToDoDisplay",
    components: {
        ToDoElement
    },
    computed: {
        getToDos() {
            return this.$store.getters.getToDos;
        }
    }
};
</script>

<style scoped>
#main {
    margin-top: 20px;
}
</style>