<template>
    <div class="container">
        <input
            class="todo"
            placeholder="ToDo"
            @keyup.enter="addToDo(toDoTask)"
            v-model="toDoTask"
            type="text"
        />
        <input
            classs="category"
            placeholder="Category"
            v-model="category"
            @keyup.enter="addToDo(toDoTask)"
            type="text"
        />
        <button @click="addToDo(toDoTask)">Submit</button>
    </div>
</template>

<script>
export default {
    name: "ToDoSubmit",
    data() {
        return {
            toDoTask: "",
            category: ""
        };
    },
    methods: {
        addToDo(toDoTask) {
            if (toDoTask != "") this.$store.dispatch("addToDo", toDoTask);
            this.toDoTask = "";
            this.category = "";
        }
    }
};
</script>

