import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export default new Vuex.Store({
    state: {
        toDoTasks: [{
                id: 0,
                name: 'Add color depending on category',
            },
            {
                id: 1,
                name: 'Think about adding new improvents',
            },
        ]
    },
    mutations: {
        ADD_TODO(state, payload) {
            state.toDoTasks.push({
                name: payload,
                id: state.toDoTasks.length,
            });
        },
        REMOVE_TODO(state, payload) {
            state.toDoTasks = state.toDoTasks.filter(function(obj) {
                return obj.id !== payload;
            });
        }
    },
    actions: {
        addToDo(context, toDo) {
            context.commit("ADD_TODO", toDo)
        },
        removeToDo(context, index) {
            context.commit("REMOVE_TODO", index);
        }
    },
    getters: {
        getToDos(state) {
            return state.toDoTasks
        }
    }
});